import numImgPair from './imgComponents/numImgPair.vue'
import numImgSelect from './imgComponents/numImgSelect.vue'
import numImgCount from './imgComponents/numImgCount.vue'
import numImgSort from './imgComponents/numImgSort.vue'
export default {
	components: {
		numImgPair,
		numImgSelect,
		numImgCount,
		numImgSort
	},
	data() {
		return {
			allData:{},
			problemList: [], //保存所有已选中的数据
			showH5: true,
			topLeftName: '数图配对',
			btnName: '下一组',
			page:{
				pageNum: 0, // 当前组数下标
				examNum: 10,
				examTime: null,
			},
			userExamTime: 0, //用户选择的答题时间数
			optionInfo:{
				collectsId: '402aa38151aef50c0151aef50c2600cc',
				time: 0,
				type: 3,
				num: 8
			},// 所有从前一个页面传过来的数据（需要传给后台的数据）
			timer: null
		};
	},
	onShow(){
		if(navigator){
			this.showH5 = true
		}else{
			this.showH5 = false
		}
		this.optionInfo = uni.getStorageSync('optionInfo')
		this.startNumExamination()
	},
	methods:{
		// 获取训练数据列表
		startNumExamination(){
			if(uni.getStorageSync('userInfo')){
				let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
				this.$Request.get('/appExaminationController.do?startNumExamination',{
					...this.optionInfo,
					memberId:memberId
				}).then(res => {
					if(res.code == 0){
						this.allData = res.data
						this.userExamTime = res.data.examTime == 0 ? '' : res.data.examTime
						this.page.examTime = this.userExamTime // 用户选择的每题时间数
						this.page.examNum = this.allData.examNum // 测试组数
						if(this.userExamTime != ''){
							this.timer = setInterval(() => {
								// 倒计时
								this.page.examTime = this.page.examTime - 1;
								// 当倒计时为0时
								if (this.page.examTime === 0) {
									// console.log('allData', this.allData)
									if(Number(this.page.pageNum)+ 1 == Number(this.page.examNum)){
										console.log('所有题都答完了')
										this.btnName = '结束答题'
										let answerList = this.problemList.filter(item => item.problemId != '￥')
										console.log(answerList)
										this.takeNumExamination(answerList)
										clearInterval(this.timer);
									}else{
										this.problemList.push({
											answerId: '￥',
											problemId: '￥',
											problemName: '',
											problemPic: ''
										})
										this.page.pageNum += 1
										this.page.examTime = this.userExamTime
									}
								}
							}, 1000);
						}
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showToast({
					title: '您尚未登录，正在跳往登录页面。。。',
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateTo({
						url:'/pages/loginAll/login'
					})
				}, 1000)
			}
		},
		clickProblemList(arr){
			this.problemList = arr
		},
		clickNext(){
			if(Number(this.page.pageNum)+ 1 == Number(this.page.examNum)){
				console.log('所有题都答完了')
				this.btnName = '结束答题'
				let answerList = this.problemList.filter(item => item.problemId != '￥')
				console.log(answerList)
				this.takeNumExamination(answerList)
				clearInterval(this.timer);
			}else{
				this.problemList.push({
					answerId: '￥',
					problemId: '￥',
					problemName: '',
					problemPic: '',
				})
				this.page.pageNum += 1
				this.page.examTime = this.userExamTime
			}
		},
		// 答题结束
		takeNumExamination(answerList){
			this.$Request.postJson('/appExaminationController.do?takeNumExamination',{
				answerList: answerList,
				id: this.allData.id
			}).then(res => {
				if(res.code == 0){
					let id = res.data.id
					uni.showToast({
						title: '提交成功，正在跳转...',
						icon: 'none'
					})
					setTimeout(()=>{
						uni.navigateTo({
							url:`/pages/train/imageMemory/numImgTestOver?id=${id}`
						})
					},1000)
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
	}
}
